import React, { useState, useEffect } from "react";
import { Table } from 'reactstrap';
import JobTable from './JobTable';
import store from '../../../store'
import axios from "axios";
import PaginationItems from './PaginationItems';
import { log } from 'async';

const ManageJob = () => {
  const [services, setServices] = useState([]);
  const user = store.getState().auth.user

  useEffect(() => {
    async function fetchData() {
      const response = await axios.get(`https://www.togedoortestgo.site/graphql/services`);
      setServices(response.data.sort((a, b) => (a.createdAt < b.createdAt) ? 1 : -1));
    }

    fetchData();
  }, []);

  return (
    <>
      {services &&
        user ? (
        <Table hover className="manage-job-table">
          <thead className="bg-primary text-white">
            <tr>
              <th className="py-4 pl-5">Project title</th>
              <th className="d-none d-md-table-cell   py-4 pr-6 text-center">
                Image
              </th>
              <th className=" d-none d-sm-table-cell py-4 p-0 text-center ">

              </th>
              <th className="py-4 pl-5 text-center">Action</th>
            </tr>
          </thead>
          <tbody className=" border mb-30">
            {services &&
              user &&
              services
                .filter((service) => service.userID === user.id)
                .map((service, i) => <JobTable key={i} {...service} />)
            }
          </tbody>
        </Table>

      ) : <div>Loading...</div>}
      <PaginationItems />
    </>
  );
};

export default ManageJob;
